<template>
    <div class="content-table">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>{{ headerCard }}</span>
            </div>
            <el-row>
                <el-col>
                    {{editDataStatusIcon}}
                    <el-form class="form-application-add" label-position="left" :model="formApplicationAdd" ref="formApplicationAdd" :rules="rules" label-width="150px">
                        <el-form-item label="Category">
                            <el-input v-model="formApplicationAdd.category" placeholder="please insert name Application"></el-input>
                        </el-form-item>
                        <el-form-item label="Icon">
                            <el-upload
                                class="upload-demo"
                                action=""
                                ref="upload"
                                list-type="picture"
                                :multiple="false"
                                :file-list="fileList"
                                :on-change="iconChange"
                                :on-remove="iconRemove"
                                name="file"
                                :auto-upload="false">
                            <el-button size="small" type="primary">Click to upload</el-button>
                            <div slot="tip" class="el-upload__tip">jpg/png files with a size less than 1024kb</div>
                            </el-upload>
                        </el-form-item>
                        
                    </el-form>
                </el-col>
            </el-row>
            <el-row>
                <el-col>
                    <div class="btn-input-add">                        
                    <el-button size="small" @click="submitForm('formApplicationAdd')" :disabled="SubmitStatus">Submit</el-button>
                    <el-button size="small" @click="cancelSubmit" type="danger">Cancel</el-button>
                    </div>
                </el-col>
            </el-row>
        </el-card>
    </div>
</template>

<script>

import axios from '../../../axios.js'
import router from '@/router'

export default {
    data () {
        var validateCategory = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('Please input Name Caegory'));
        } else {
          callback();
        }
        };
        return {
            editDataStatusIcon : false,
            formApplicationAdd : {
                category : '',
            },
            rules: {
            category: [
                { validator: validateCategory, trigger: 'blur' }
            ],
            },
            fileList: [{
                name: '',
                url: ''
                }],
            formData : new FormData(),
            SubmitStatus : false
        }
    },
    computed : {
        baseUrl () {
            const path = this.$route.path
            return path[0]
        },
        headerCard () {
            return this.$route.name
        },
        detailData () {
            return this.data
        }
        
    },
    methods : {
        iconChange (file, fileList) {
            this.SubmitStatus = false
            this.fileList = fileList.slice(-1)
            this.editDataStatusIcon = true
        },
        iconRemove (file, fileList) {
            this.SubmitStatus = true
            this.fileList = fileList.slice(-1)
            this.editDataStatusIcon = true
        },
        cancelSubmit () {
            router.push('/category')
        },
        submitForm(formName) {
        this.loading = true
        this.$refs[formName].validate((valid) => {
            if (valid) {
            const obj = JSON.stringify({
              id_user : this.$store.state.userDataActive.id,
              id_categories : this.$route.params.id,
              categories : this.formApplicationAdd.category,
              editIcon : this.editDataStatusIcon,
            });
            this.formData.append('file', this.$refs.upload.uploadFiles[0].raw)
            this.formData.append('item', obj)
            axios({
                method: 'post',
                url: `categories/${this.$route.params.id}/edit`,
                data: this.formData,
                headers: {
                'Content-Type': 'multipart/form-data',
                'Authorization': `Bearer ${localStorage.getItem('accessToken')}`
                }
            
            })
            .then((response) => {
            if (response.data.success == true) {
                router.push('/category')
            } else {
                this.loading = false
                this.$notify.error({
                    title: 'Error',
                    message: 'Invalid Data Input',
                    offset: 100
                })
            }
            })

          } else {
            this.loading = false
            this.$notify.error({
              title: 'Error',
              message: 'Invalid Data Input',
              offset: 100
            });
          }
        })
        }
    },
    async created () {
        const accessToken = {
            headers: { Authorization: `Bearer ${localStorage.getItem('accessToken')}` }
        }
        await axios.get(`categories/${this.$route.params.id}/detail`, accessToken)
            .then((response) => {
                this.formApplicationAdd.category = response.data.data.name_categories
                this.fileList[0].name = response.data.data.name+'.'+response.data.data.extension
                this.fileList[0].url = response.data.data.url
        })
    }
    
}
</script>

<style>
.form-application-add {
    margin-right: 25px;
    margin: 0 auto;
    width: 600px;
}
.content-table {
    margin: 0;
    padding-left: 25px;
    padding-right: 25px;
}
.btn-input-add {
    margin: 0 auto;
    width: 250px;
}
</style>
